import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { CasualtyPolicy } from 'src/app/models/casualty-policy';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CasualtyPolicyService {
  baseUrl = '/api/casualties/';
  policies = '/policies';

  constructor(private hc: HttpClient) { }

  private buildUsualHeader() {
    const session = window.localStorage.getItem('_session') || 'notloggedin';
    return {
      headers: {
        's-t': session,
      }
    };
  }

  getList(token: string): Observable<CasualtyPolicy[]> {
    return this.hc.get<CasualtyPolicy[]>(
      `${this.baseUrl}${token}${this.policies}`,
      this.buildUsualHeader()
    );
  }
  single(token: string, id: string): Observable<CasualtyPolicy> {
    return this.hc.get<CasualtyPolicy>(
      `${this.baseUrl}${token}${this.policies}/${id}`,
      this.buildUsualHeader()
    );
  }
  saveNew(token: string, cp: CasualtyPolicy): Observable<CasualtyPolicy> {
    return this.hc.post<CasualtyPolicy>(
      `${this.baseUrl}${token}${this.policies}`,
      cp,
      this.buildUsualHeader()
    );
  }
  update(token: string, id: string, cp: CasualtyPolicy): Observable<CasualtyPolicy> {
    return this.hc.put<CasualtyPolicy>(
      `${this.baseUrl}${token}${this.policies}/${id}`,
      cp,
      this.buildUsualHeader()
    );
  }
  delete(token: string, id: string): Observable<boolean> {
    const session = window.localStorage.getItem('_session') || 'notloggedin';
    return new Observable<boolean>((observer) => {
      this.hc.delete(
        `${this.baseUrl}${token}${this.policies}/${id}`,
        {
          headers: {
            's-t': session,
          },
          observe: 'response',
          responseType: 'text',
        }
      ).subscribe((res: HttpResponse<any>) => {
        observer.next(res.status === 200);
      }, (error) => {
        observer.error(error);
      });
    });
  }
}
