import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Casualty } from '../models/casualty';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CasualtyService {
  baseUrl = '/api/casualties';
  constructor(private hc: HttpClient) { }

  getList(kind?: string): Observable<Casualty[]> {
    let session = window.localStorage.getItem('_session');
    if (! session) {
      session = 'loggedout';
    }
    return this.hc.get<Casualty[]>(`${this.baseUrl}`, {
      headers : {
        's-t': session
      }
    });
  }

  single(token: string): Observable<Casualty> {
    let session = window.localStorage.getItem('_session');
    if (! session) {
      session = 'loggedou';
    }
    return this.hc.get<Casualty>(`${this.baseUrl}/${token}`, {
      headers: {
        's-t': session
      }
    });
  }
  saveNew(casualty: Casualty): Observable<Casualty> {
    let session = window.localStorage.getItem('_session');
    if (! session) {
      session = 'loggedou';
    }
    return this.hc.post<Casualty>(this.baseUrl, casualty, {
      headers: {
        's-t': session
      }
    });
  }

  update(casualty: Casualty): Observable<Casualty> {
    let session = window.localStorage.getItem('_session');
    if (! session) {
      session = 'loggedou';
    }
    return this.hc.put<Casualty>(`${this.baseUrl}/${casualty.token}`, casualty, {
      headers: {
        's-t': session
      }
    });
  }

  delete(token: string): Observable<boolean> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.hc.delete(`${this.baseUrl}/${token}`, {
        headers: {
          's-t': session
        },
        observe: 'response',
        responseType: 'text',
      }).subscribe((res: HttpResponse<any>) => {
        observer.next(res.status === 200);
      }, error => {
        observer.error(error);
      });
    });
  }

}
