import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import { ToastController, AlertController, ModalController } from '@ionic/angular';
import { CasualtyPolicy } from 'src/app/models/casualty-policy';
import { CasualtyPolicyService } from 'src/app/services/casualty-policy.service';
import { Casualty } from 'src/app/models/casualty';
import { CasualtyService } from 'src/app/services/casualty.service';
import { PolicyModalPage } from 'src/app/modals/policy-modal/policy-modal.page';
import { OrganizationModalPage } from 'src/app/modals/organization-modal/organization-modal.page';

@Component({
  selector: 'app-casualty-policies',
  templateUrl: './casualty-policies.page.html',
  styleUrls: ['./casualty-policies.page.scss'],
})
export class CasualtyPoliciesPage implements OnInit, OnDestroy {
  listLabel = 'Infortuni';
  backLabel = 'Infortunio';
  route$: Subscription;
  token = '';
  entries: CasualtyPolicy[] = [];
  cpForm: FormGroup;
  casualty: Casualty;

  constructor(
    private mc: ModalController,
    private fb: FormBuilder,
    private router: Router,
    private ar: ActivatedRoute,
    private tc: ToastController,
    private ac: AlertController,
    private cps: CasualtyPolicyService,
    private cs: CasualtyService,
  ) {
    this.cpForm = fb.group({
      casualtyNumber: [''],
      tokenPolicy: [''],
      policyName: [''],
      idIspettorato: [''],
      idIstruttore: [''],
      idMedicoLegale: [''],
      idLiquidatore: [''],
      nomeIspettorato: [''],
      nomeIstruttore: [''],
      nomeMedicoLegale: [''],
      nomeLiquidatore: [''],
      dataLetteraIntervento: [''],
      numeroRaccomandata: [''],
      notes: [''],
      inviataAR: [false],
      inviataMail: [false],
      inviatoFax: [false],
      status: ['aperto'],
    });
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.route$) {
      this.route$.unsubscribe();
    }
  }

  private getToken(): Promise<string> {
    const self = this;
    return new Promise((resolve, reject) => {
      self.route$ = self.ar.params.subscribe((params) => {
        resolve(params.token);
      });
    });
  }

  private getList(token: string): Promise<CasualtyPolicy[]> {
    const self = this;
    return new Promise((resolve, reject) => {
      self.cps.getList(token).subscribe({
        next(list) {
          resolve(list);
        },
        error(err) {
          reject(err);
        }
      });
    });
  }

  getCasualty(token: string): Promise<Casualty> {
    const self = this;
    return new Promise((resolve, reject) => {
      this.cs.single(token).subscribe({
        next(casualty) {
          resolve(casualty);
        },
        error(err) {
          reject(err);
        }
      });
    });
  }

  async onIspettoratoChooseClick() {
    const self = this;
    const modal = await this.mc.create({
      component: OrganizationModalPage,
      componentProps: {
        kind: 'Ispettorati',
      }
    });
    await modal.present();
    const { data } = await modal.onDidDismiss();
    if (data) {
      this.cpForm.get('idIspettorato').setValue(data._id);
      this.cpForm.get('nomeIspettorato').setValue(data.name);
    }
  }

  resetIspettoratoClick() {
    this.cpForm.get('idIspettorato').setValue('');
    this.cpForm.get('nomeIspettorato').setValue('');
  }

  async onIstruttoreChooseClick() {
    const self = this;
    const modal = await this.mc.create({
      component: OrganizationModalPage,
      componentProps: {
        kind: 'Periti',
      }
    });
    await modal.present();
    const { data } = await modal.onDidDismiss();
    if (data) {
      this.cpForm.get('idIstruttore').setValue(data._id);
      this.cpForm.get('nomeIstruttore').setValue(data.name);
    }
  }

  resetIstruttoreClick() {
    this.cpForm.get('idIstruttore').setValue('');
    this.cpForm.get('nomeIstruttore').setValue('');
  }

  async onMedicoChooseClick() {
    const self = this;
    const modal = await this.mc.create({
      component: OrganizationModalPage,
      componentProps: {
        kind: 'Medici',
      }
    });
    await modal.present();
    const { data } = await modal.onDidDismiss();
    if (data) {
      this.cpForm.get('idMedicoLegale').setValue(data._id);
      this.cpForm.get('nomeMedicoLegale').setValue(data.name);
    }
  }

  resetMedicoClick() {
    this.cpForm.get('idMedicoLegale').setValue('');
    this.cpForm.get('nomeMedicoLegale').setValue('');
  }

  async onLiquidatoreChooseClick() {
    const self = this;
    const modal = await this.mc.create({
      component: OrganizationModalPage,
      componentProps: {
        kind: 'Liquidatori',
      }
    });
    await modal.present();
    const { data } = await modal.onDidDismiss();
    if (data) {
      this.cpForm.get('idLiquidatore').setValue(data._id);
      this.cpForm.get('nomeLiquidatore').setValue(data.name);
    }
  }

  resetLiquidatoreClick() {
    this.cpForm.get('idLiquidatore').setValue('');
    this.cpForm.get('nomeLiquidatore').setValue('');
  }

  async onPolicyChooseClick() {
    const self = this;
    const modal = await this.mc.create({
      component: PolicyModalPage,
      componentProps: {
        customerToken: self.casualty.customerId,
      }
    });
    await modal.present();
    const  { data } = await modal.onDidDismiss();
    if (data) {
      this.cpForm.get('tokenPolicy').setValue(data.policy.token);
      this.cpForm.get('policyName').setValue(`${data.insuranceCompany.name} ${data.policy.number}`);
    }
  }

  resetPolicyClick() {
    this.cpForm.get('tokenPolicy').setValue('');
    this.cpForm.get('policyName').setValue('');
  }

  onListaClick() {
    this.router.navigate(['home']);
  }

  onBackClick() {
    this.router.navigate(['casualty', this.token]);
  }

  async ionViewWillEnter() {
    try {
      this.token = await this.getToken();
      this.backLabel = 'Infortunio';
      this.listLabel = 'Lista infortuni';
      this.entries = await this.getList(this.token);
      this.casualty = await this.getCasualty(this.token);
    } catch (err) {
      console.error(err);
    }
  }

  private async showMessage(message: string) {
    const toast = await this.tc.create({
      message,
      duration: 2000
    });
    await toast.present();
  }

  private resetForm() {
    this.cpForm.patchValue({
      casualtyNumber: '',
      tokenPolicy: '',
      policyName: '',
      idIspettorato: '',
      idIstruttore: '',
      idMedicoLegale: '',
      idLiquidatore: '',
      nomeIspettorato: '',
      nomeIstruttore: '',
      nomeMedicoLegale: '',
      nomeLiquidatore: '',
      dataLetteraIntervento: '',
      numeroRaccomandata: '',
      notes: '',
      inviataAR: false,
      inviataMail: false,
      inviatoFax: false,
      status: 'aperto',
    });
  }

  private saveNew(toSave: CasualtyPolicy): Promise<CasualtyPolicy> {
    const self = this;
    return new Promise((resolve, reject) => {
      self.cps.saveNew(this.token, toSave).subscribe({
        next(cp) {
          resolve(cp);
        },
        error(err) {
          reject(err);
        }
      });
    });
  }

  async onSaveClick() {
    try {
      const toSave: CasualtyPolicy = this.cpForm.value;
      const saved = await this.saveNew(toSave);
      this.entries = [saved, ...(this.entries)];
      this.resetForm();
      this.showMessage(`Il salvataggio è andato a buon fine.`);
    } catch (err) {
      this.showMessage(`Qualcosa è andato storto: ${err}. Riprova più tardi`);
    }
  }

  private delete(cp: CasualtyPolicy): Promise<boolean> {
    const self = this;
    return new Promise((resolve, reject) => {
      self.cps.delete(this.token, cp._id).subscribe({
        next(done) {
          resolve(done);
        },
        error(err) {
          reject(err);
        }
      });
    });
  }

  private async confirmDelete(cp: CasualtyPolicy) {
    const self = this;
    const alert = await this.ac.create({
      header: 'Cancellazione voce',
      message: 'Stai per <strong>cancellare</strong> una voce',
      buttons: [
        {
          text: 'Annulla',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (cnl) => {}
        },
        {
          text: 'Conferma',
          handler: async () => {
            try {
              const done = await this.delete(cp);
              if (done) {
                this.entries = this.entries.filter(e => {
                  return e._id !== cp._id;
                });
                await self.showMessage(`Cancellazione avvenuta con successo.`);
              } else {
                await self.showMessage(`Si è verificato un problema. Riprova!`);
              }
            } catch (e) {
              await self.showMessage(`Si è verificato un problema ${e}. Riprova!`);
            }
          }
        }
      ]
    });
    await alert.present();
  }

  async onDeleteEntryClick(cp: CasualtyPolicy) {
    await this.confirmDelete(cp);
  }

  onEditEntryClick(cp: CasualtyPolicy) {
    this.router.navigate(['casualty', this.token, 'policies', cp._id]);
  }
}
